.CartItem td:nth-child(1) {
    max-width: 350px;
    width: 350px;
}

.CartItem td:nth-child(1) p {
    margin: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

/* .CartItem td:nth-child(3) {
    max-width: 120px;
    width: 120px;
} */

@media (max-width: 767px) {
    .CartItem td:nth-child(1) {
        max-width: 200px;
        width: 200px;
    }

    .CartItem td:nth-child(2) {
        display: none;
    }
}