.Cart .count-button {
    margin: 0 5px;
    padding: 1px 6px;
}

.Cart .remove-button {
    padding: 1px 6px;
}

.Cart .CartBottom {
    display: flex;
    justify-content: space-between;
}

.Cart .CartButtons button {
    margin: 0 10px;
}

@media (max-width: 767px) {
    .Cart th:nth-child(2) {
        display: none;
    }
}

@media (max-width: 575px) {
    .Cart thead {
        display: none;
    }
}