.CartIcon {
    z-index: 10;
    position: fixed;
    top: 20px;
    right: 20px;
}

.CartIcon .CartIconContainer {
    position: relative;
}

.CartIcon i {
    font-size: 50px;
    color: darkgray;
}

.CartIcon .CartIconCount {
    position: absolute;
    bottom: 0;
    left: 0;
    transform: translateY(25%);

    font-size: 12px;
    background: red;
    color: #fff;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.CartIcon .CartIconCount span {
    transform: translateY(-1px);
}